@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
:root{
    --gian: #00bf8f; 
}

body{
    /* font-family: 'Roboto', sans-serif; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    overflow-x: hidden;
    background: #00bf8f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right bottom, #001510, #00bf8f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-rendering: optimizelegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.container-main{
    width: 100vw;
    /* margin: 1rem; */

}
.row{
    height: 95vh;
    margin: 0 auto;
    /* width: 95vw; */
}

.row>*>*{
    padding: 1rem;
}
.app__sidebar{
    height: 95vh;
    /* position: absolute; */
    border-radius: 1rem;
    margin: .5rem;
    background-color: #fff;
}
.app__main-content{
    overflow-x: hidden;
    overflow-y: auto;
    height: 95vh;
    max-height: 95vh;
    margin: .5rem;
    border-radius: 1rem;
    background-color: #fff;    
}

hr{
    background-color: #2a0845;
    color: #2a0845;
    width: 100px;
    height: 2.5px;
}

.navbar__items{
    display: flex;
    flex-direction: row;
    /* width: 230px; */
    font-size: large;
    /* justify-content: space-between; */
    font-weight: 700;
}
.navbar__item{
    margin: 10px;

}
.navbar__items>*{
    text-decoration: none;
    color: black;
}
.navbar__item:hover{
    color: var(--gian);
    text-decoration: none;
}

.navbar__active{
    font-size: 2.5rem;
    font-weight: 700;
    border-bottom: 7px solid var(--gian);
}
.last-name{
    color: var(--gian);
}

.sidebar{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    justify-content: space-between;
}

.sidebar__name{
    font-weight: 700;
}
.sidebar__logo{
    width: 11rem;
    text-align: center;
    /* height: 100%;     */
    /* transform: translateX(-5rem); */
    background-color:  #00bf8f
}

.sidebar__logo-container{
    margin: 0 auto;
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    overflow: hidden;
}

.sidebar__title{
    border-radius: 500px;
    background: linear-gradient(to bottom, #00151083, #00bf8f88);
    padding: 1rem;
    /* color: aliceblue; */
}
.sidebar__nav{
    text-align: left;
    height: 40%;
    width: 80%;
    font-size: larger;
    border: unset;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.sidebar__nav>*{
    border: 20px black;
    border-radius: .5rem;
    padding: .5rem;
    height: 3rem;
    background:linear-gradient(to right bottom, #001510, #00bf8f);
}
a{
    color: aliceblue;
}
.sidebar__nav>*>*:hover{
    color: aliceblue;
    text-decoration: none;
}
.sidebar__buttons{
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-evenly;
}


.sidebar__contact{
    background-color:  #00bf8f2d;
    padding: .75rem;
    border-radius: 20px;
}
.sidebar__contact>*{
    margin: 2px;
}

.btn.login-button{
    background-color: #00bf8f;  /* fallback for old browsers */

}


.home__container{
    padding: 1rem;
}
.home__info{
    font-weight:400;
}

.home__feed-container{
    background-color:#00bf8f21;
    /* box-shadow:  0px 0px 10px 10px #00000049; */
    border-radius: 30px;
    padding: 2rem;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.home__feed-title{
    font-weight: 700;
    text-transform: uppercase;
    border-bottom: 2px solid #00bf8f;
}

.home__feed-placeholder{
    padding: 12rem 0;
    text-align: center;
    font-size: 1.5rem;
}

.post-img{
    width: 150px;
    border-radius: 500px;
    object-fit: contain;
}
.landing-container{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    height: 80%;
    width: 70%;
    margin: auto;
    /* background-color: #f0f; */
}
article{
    font-size: 1.25rem;
    font-weight: bold;
}


.buttons>*{
    margin: 10px;
}

.start>*{
    background-color: var(--gian);
    border-radius: 50%;
    font-size: 5rem;
    border: unset;
    height: 6rem;
    width: 6rem;
}
.sign_in_form, .sign_up_form, .profile_form{
    /* width: 400px; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* background-color: aqua; */
    height: 85%;
}

.form-background{
    background-color: rgba(0, 0, 0, 0.198);
    padding: 20px;
    border-radius: 20px;
    width: minmax(300px,50%);
}

.sign_in_form-head, .sign_up_form-head{
    font-size: 1rem;
    margin-bottom: 2em;
}
.form-button{
    text-align: center;
    margin-bottom: 1em;
}

h1{
    font-size: 1.25rem;
    font-weight: 700;
    border-bottom: 3px solid var(--gian);

}
.profile_form{
    width: 100%;
}
.profile_form .form-background{
    width: 100%;
    min-height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.profile_form form{
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 35% 55%;
    
}
.multiSelectContainer{
    background-color: white;
    border-radius: 4px;
}
* {
    box-sizing: border-box;
  }
  
  :root {
    --background: white;
    --primary: #ff1ead;
    --secondary: #1effc3;
    --card-size: 300px;
  }
  
  /* body { */
    /* height: 100vh; */
    /* margin: 0; */
    /* display: grid;
    place-items: center;
    padding: 1rem; */
    /* background: var(--background); */
    /* font-family: "Source Code Pro", monospace; */
    
  /* } */
  
  .card {
    width: calc(300px * 1.586);
    width: calc(var(--card-size) * 1.586);
    height: 300px;
    height: var(--card-size);
    border-radius: 0.75rem;
    box-shadow: 0 22px 70px 4px rgba(0, 0, 0, 0.56), 0 0 0 1px rgba(0, 0, 0, 0.3);
    background: rgba(0, 0, 0, 0.876);
    display: grid;
    grid-template-columns: 40% auto;
    margin: 2rem;
    color: white;
    align-items: center;
    will-change: transform;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  }
  .card:hover {
    transform: scale(1.05);
    box-shadow: 0 32px 80px 14px rgba(0, 0, 0, 0.36), 0 0 0 1px rgba(0, 0, 0, 0.3);
  }
  
  .card-details {
    padding: 1rem;
  }
  
  .name {
    font-size: 1.25rem;
  }
  
  .occupation {
    font-weight: 600;
    color: #ff1ead;
    color: var(--primary);
  }
  
  .card-avatar {
    display: grid;
    place-items: center;
  }
  
  svg {
    fill: white;
    width: 65%;
  }
  
  .card-about {
    margin-top: 1rem;
    display: grid;
    grid-auto-flow: column;
  }
  
  .item {
    display: flex;
    flex-direction: column;
    margin-bottom: 0.5rem;
  }
  .item .value {
    font-size: 1rem;
  }
  .item .label-card {
    margin-top: 0.15rem;
    font-size: 0.75rem;
    font-weight: 600;
    color: #ff1ead;
    color: var(--primary);
  }
  
  .skills {
    display: flex;
    flex-direction: column;
    margin-top: 0.75rem;
  }
  .skills .label-card {
    font-size: 1rem;
    font-weight: 600;
    color: #ff1ead;
    color: var(--primary);
  }
  .skills .value {
    margin-top: 0.15rem;
    font-size: 0.75rem;
    line-height: 1.25rem;
  }
 
  .label{
    display: inline;
    padding: .2em .6em .3em;
    font-size: 85%;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25em;
    background-color: #ff1ead;
    background-color: var(--primary);
    margin: 0 .25rem;
  }
