.profile_form{
    width: 100%;
}
.profile_form .form-background{
    width: 100%;
    min-height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.profile_form form{
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 35% 55%;
    
}
.multiSelectContainer{
    background-color: white;
    border-radius: 4px;
}