* {
    box-sizing: border-box;
  }
  
  :root {
    --background: white;
    --primary: #ff1ead;
    --secondary: #1effc3;
    --card-size: 300px;
  }
  
  /* body { */
    /* height: 100vh; */
    /* margin: 0; */
    /* display: grid;
    place-items: center;
    padding: 1rem; */
    /* background: var(--background); */
    /* font-family: "Source Code Pro", monospace; */
    
  /* } */
  
  .card {
    width: calc(var(--card-size) * 1.586);
    height: var(--card-size);
    border-radius: 0.75rem;
    box-shadow: 0 22px 70px 4px rgba(0, 0, 0, 0.56), 0 0 0 1px rgba(0, 0, 0, 0.3);
    background: rgba(0, 0, 0, 0.876);
    display: grid;
    grid-template-columns: 40% auto;
    margin: 2rem;
    color: white;
    align-items: center;
    will-change: transform;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  }
  .card:hover {
    transform: scale(1.05);
    box-shadow: 0 32px 80px 14px rgba(0, 0, 0, 0.36), 0 0 0 1px rgba(0, 0, 0, 0.3);
  }
  
  .card-details {
    padding: 1rem;
  }
  
  .name {
    font-size: 1.25rem;
  }
  
  .occupation {
    font-weight: 600;
    color: var(--primary);
  }
  
  .card-avatar {
    display: grid;
    place-items: center;
  }
  
  svg {
    fill: white;
    width: 65%;
  }
  
  .card-about {
    margin-top: 1rem;
    display: grid;
    grid-auto-flow: column;
  }
  
  .item {
    display: flex;
    flex-direction: column;
    margin-bottom: 0.5rem;
  }
  .item .value {
    font-size: 1rem;
  }
  .item .label-card {
    margin-top: 0.15rem;
    font-size: 0.75rem;
    font-weight: 600;
    color: var(--primary);
  }
  
  .skills {
    display: flex;
    flex-direction: column;
    margin-top: 0.75rem;
  }
  .skills .label-card {
    font-size: 1rem;
    font-weight: 600;
    color: var(--primary);
  }
  .skills .value {
    margin-top: 0.15rem;
    font-size: 0.75rem;
    line-height: 1.25rem;
  }
 
  .label{
    display: inline;
    padding: .2em .6em .3em;
    font-size: 85%;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25em;
    background-color: var(--primary);
    margin: 0 .25rem;
  }